body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
@font-face {
    font-family: 'gothic';
    src: url(/static/media/gothic.9a7bcf3c.eot);
    src: local('gothic'), url(/static/media/gothic.151c4696.woff) format('woff'), url(/static/media/gothic.34cc487b.ttf) format('truetype');
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.resume-container {
    height: 100%;
    box-shadow: -2px 0px 20px 3px #747474;
}

.fullHeight {
    height: 100%;
}

html, body, #root {
    height: 100%;
    background: #a2a2a2;
}

a {
    color: inherit;
}

